<template>
  <div class="control">
    <audio preload="auto" :src="song"></audio>
    <i
      class="pre"
      @@click="pre">
      <i></i>
      <i></i>
    </i>
    <i @click="play" :class="[!isPlayed ? 'pause-btn' : 'play-btn']">
      <i></i>
      <i></i>
      <i></i>
    </i>
    <i
      class="next"
      @click="next">
      <i></i>
      <i></i>
    </i>
    <i
      class="progress">
      <i></i>
    </i>
    <i
      class="duration">{{fomatTime(duration)}}</i>
  </div>
</template>

<script>
export default {
  props: ['songs', 'current'],
  name: 'player',
  data () {
    return {
      player: '',
      progress: '',
      duration: 0,
      isPlayed: false,
      ended: false,
      timer: null,
      percent: 0,
      song: '',
      currIndex: -1,
      songCount: this.songs.length
    }
  },
  mounted () {
    this.player = document.querySelector('audio')
    this.progress = document.querySelector('.progress>i')
    setInterval(() => {
      this.currIndex = this.current.index
    }, 500)
  },
  methods: {
    play () {
      if (!this.isPlayed && this.song) {
        this.player.play()
        this.isPlayed = true
        this.startInterval()
      } else {
        this.stopInterval()
        this.player.pause()
        this.isPlayed = false
      }
    },
    pre () {
      this.isPlayed = true
      this.current.index--
      this.current.index === 0
      ? this.current.index = 0
      : true
      this.song = this.songs[this.current.index]
    },
    next () {
      this.percent = 0
      this.isPlayed = true
      this.current.index++
      this.current.index === this.songCount - 2
      ? this.current.index = this.songCount - 2
      : true
      this.song = this.songs[this.current.index]
    },
    progr (percent) {
      this.progress.style.transformOrigin = 'left'
      this.progress.style.transform = 'scaleX(' + percent + ')'
    },
    startInterval () {
      this.timer = setInterval(() => {
        this.percent += 0.2
        this.progr(this.percent)
        // console.log(this.percent)
        if (this.player.ended) {
          this.isPlayed = false
          this.next()
        }
      }, this.duration)
      console.log(this.duration)
    },
    stopInterval () {
      clearInterval(this.timer)
    },
    fomatTime (time) {
      time = Math.floor(time)
      time = Math.floor(time / 60) + ':' + time % 60
      return time
    }
  },
  watch: {
    currIndex: function () {
      this.song = this.songs[this.currIndex]
      setTimeout(() => {
        this.duration = this.player.duration
        this.player.play()
        this.isPlayed = true
      }, 500)
    },
    isPlayed: function () {
      this.percent = 0
      this.progress.style.transform = 'scale(0)'
      if (this.isPlayed) {
        this.startInterval()
      } else {
        this.stopInterval()
      }
    }
  }
}
</script>

<style lang='less'>
@bili: 350/1280px;
  .control{
    float: right;
    width: 350px;
    border-radius: 15px;
    text-align: center;
    line-height: 80px;
    background: rgba(0, 0, 0, .5);
    .pre, .next{
      display: inline-block;
      vertical-align: middle;
      width: 30px;
      height: 30px;
      line-height: 30px;
      margin: 0 20px;
      cursor: pointer;
      i{
        display: inline-block;
        &:nth-child(1){
          width: 4px;
          height: 30px;
          border-radius: 15px;
          background: #fde;
        }
        &:nth-child(2){
          width: 0;
          height: 0;
          border-radius: 5px;
          border: 15px solid transparent;
          border-right: 15px solid #fde;
          margin-left: -22px;
        }
      }
    }
    .next{
      transform: rotate(180deg)
    }
    .pause-btn, .play-btn{
      display: inline-block;
      vertical-align: middle;
      width: 34px;
      height: 34px;
      cursor: pointer;
      i{
        position: absolute;
        display: inline-block;
        width: 30px;
        height: 4px;
        background: #fde;
        transform-origin: left;
        transition: all 1s;
      }
    }
    .pause-btn{
      text-align: left;
      i{
        display: inline-block;
        width: 30px;
        height: 4px;
        background: #fde;
        transform-origin: left;
        transition: all 1s;
        &:nth-child(1) {
          transform: translateX(5px) rotate(90deg)
        }
        &:nth-child(2) {
          transform: translateX(5px) rotate(30deg)
        }
        &:nth-child(3) {
          transform: translate(5px, 30px) rotate(-30deg)
        }
      }
    }
    .play-btn{
      i{
        &:nth-child(1) {
          background: transparent
        }
        &:nth-child(2) {
          transform: rotate(90deg) translateY(-10px)
        }
        &:nth-child(3) {
          transform: rotate(90deg) translateY(10px)
        }
      }
    }
    .progress{
      display: inline-block;
      position: relative;
      vertical-align: middle;
      width: 200px;
      height: 10px;
      background: #ddd;
      i{
        display: inline-block;
        position: absolute;
        left: 0;
        top: 0;
        width: 1px;
        height: 10px;
        background: #f00;
      }
    }
    .duration{
      color: #fff;
      margin-left: 50px;
    }
  }
/* 窗口宽度<960,设计宽度=768 */
@media screen and (max-width:959px){

}
/* 窗口宽度<768,设计宽度=640 */
@media screen and (max-width:767px){
  .control{
    float: none;
    width: 640px;
  }
}
/* 窗口宽度<640,设计宽度=480 */
@media screen and (max-width:639px){
  .control{
    float: none;
    width: 480px;
  }
}
/* 窗口宽度<480,设计宽度=320 */
@media screen and (max-width:479px){
}
</style>



