<template>
  <table class="song-list">
    <thead>
      <tr>
        <th width="50%">歌曲</th>
        <th width="25%">歌手</th>
        <th width="25%">时长</th>
      </tr>
    </thead>
    <tbody>
      <tr
        v-for="(list, key) in lists"
        :key="list.id"
        @click="sendCurrent(key)">
        <td>{{fomat(list.title)}}</td>
        <td>{{fomat(list.author)}}</td>
        <td>3:00</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  name: 'list',
  props: ['lists', 'current'],
  data () {
    return {
      curr: -1
    }
  },
  methods: {
    fomat (str) {
      str = str.replace(/<[/]?em>/g, '')
      return str
    },
    sendCurrent (index) {
      this.curr = index
      setTimeout(() => {
        this.$emit('getCurrent', index)
      }, 200)
    }
  }
}
</script>

<style lang='less'>
@bili: 850/1280px;
  .song-list{
    float: left;
    width: 850px;
    transition: all .5s;
    tr{
      border-bottom: 1px dashed #999;
      cursor: pointer;
      &:hover{
        background: rgba(200, 100, 50, 0.5)
      }
      &:nth-child(2n) {
        background: rgba(0, 0, 0, 0.2)
      }
    }
    td, th{
      height: 40px;
    }
  }
/* media */
/* 窗口宽度<960,设计宽度=768 */
@media screen and (max-width:959px){
  .song-list{
    width: 768 * @bili
  }
}
/* 窗口宽度<768,设计宽度=640 */
@media screen and (max-width:767px){
  .song-list{
    float: none;
    width: 640px;
  }
}
/* 窗口宽度<640,设计宽度=480 */
@media screen and (max-width:639px){
  .song-list{
  }
}
/* 窗口宽度<480,设计宽度=320 */
@media screen and (max-width:479px){
  .song-list{
  }
}
</style>



