<template>
  <div class="music">
    <search
      :data="data"
      @getLists="setLists"></search>
    <list
      name="list"
      :current="current"
      :lists="lists"
      @getCurrent="setCurrent"></list>
    <player
      name="player"
      :current="current"
      :songs="data"></player>
  </div>
</template>

<script>
import Search from './Search'
import List from './List'
import Player from './Player'

export default {
  name: 'music',
  components: {Search, List, Player},
  data () {
    return {
      data: [],
      lists: '',
      current: {index: -1}
    }
  },
  mounted () {
    this.setCookie('appver', '2.0.2')
    this.setCookie('referer', 'http://music.163.com')
  },
  methods: {
    setCookie (name, value) {
      let Days = 30
      let exp = new Date()
      exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000)
      document.cookie = name + '=' + escape(value) + ';expires=' + exp.toGMTString()
    },
    setCurrent (index) {
      this.current.index = index
      console.log(this.current.index)
    },
    setLists (lists, data) {
      this.lists = lists
      this.data = data
    }
  }
}
</script>

<style lang="less" scoped>
.transform (@style){
  -webkit-transform: @style;
  transform: @style
}
.music{
  margin-top: 50px;
  overflow: hidden
}
header {
  margin-bottom: 20px;
}
.info{}

</style>



