<template>
  <header>
    <input type="text" v-model="musicName" placeholder="请输入歌名">
    <button @click="search(musicName)">搜索</button>
    <select v-model="apiName">
      <option
      v-for="api in apis"
      :key="api.id">{{api.name}}</option>
    </select>
    <ul class="tools">
      <li>收藏</li>
      <li>添加到</li>
      <li>下载</li>
      <li>删除</li>
      <li>清空列表</li>
    </ul>
  </header>
</template>

<script>
export default {
  name: 'search',
  data () {
    return {
      musicName: '',
      apiName: '',
      apis: [
        {name: '百度音乐', link: 'http://tingapi.ting.baidu.com/v1/restserver/ting/?method=baidu.ting.search.common&query='},
        {name: '网易云', link: 'http://music.163.com/api/search/get/'},
        {name: '酷狗', link: 'http://lib9.service.kugou.com/websearch/index.php?page=1&keyword='}
      ],
      lists: '',
      data: []
    }
  },
  methods: {
    search (name) {
      let api = this.apis[0].link
      for (let i in this.apis) {
        if (this.apis[i].name === this.apiName) {
          api = this.apis[i].link
        }
      }
      if (this.apiName === '酷狗') {
        api += name + '&cmd=100&pagesize=9'
      }
      console.log(api)
      if (this.apiName === '网易云') {
        this.$http.post(api, {s: name, limit: 10}).then(res => {
          return res.body
        })
      } else {
        this.$http.jsonp(api + name).then(res => {
          this.data = []
          this.lists = res.body.song_list
          for (let i in this.lists) {
            this.getUrl(this.lists[i]['song_id'])
          }
        })
      }
      this.$emit('getLists', this.lists, this.data)
    },
    getUrl (songid) {
      this.$http.jsonp('http://tingapi.ting.baidu.com/v1/restserver/ting/?method=baidu.ting.song.play&songid=' + songid).then(res => {
        res.body = JSON.stringify(res.body)
        res.body = JSON.parse(res.body)
        this.data.push(res.body['bitrate']['file_link'])
      })
    }
  }
}
</script>

<style lang='less' scoped>
  header{
    input{
      width: 150px;
      height: 40px;
      border: 1px solid #ddd;
      padding: 0 1.5em
    }
    button{
      width: 60px;
      height: 40px;
      border: none;
      border-radius: 10px;
    }
    .tools{
      margin-top: 15px;
      li{
        display: inline-block;
        width: 80px;
        height: 40px;
        line-height: 40px;
        border: 1px solid #ddd;
        border-radius: 10px;
        text-align: center;
      }
    }
  }
</style>



